import Vue from 'vue'
import ColumnRender from './ColumnRender'
import ElTableFix from './ElTableFix'

// é»è®¤åé¡µéç½®
const defaultPager = {
  currentPage: 1,
  pageSizes: [ 10, 20, 40, 50, 100 ],
  pageSize: 20,
  layout: 'total, sizes, ->, prev, pager, next, jumper',
  background: true,
  total: 0
}

/* è¡¨æ ¼ç»ä»¶ */
export default {
  name: 'ElTableExt',

  components: { ColumnRender, ElTableFix },

  props: {
    // åæ°æ®
    columns: {
      type: Array,
      default: () => []
    },

    // éæåè¡¨æ°æ®
    data: {
      type: Array,
      default: () => []
    },

    // æ¥è¯¢åè¡¨æ°æ®
    query: Function,

    // åé¡µéç½®
    pager: {
      type: Object,
      default: () => ({})
    },

    // æ¯å¦èªå¨å è½½
    autoLoad: {
      type: Boolean,
      default: true
    },

    // é»è®¤æåº
    defaultSort: Object,

    // åè¡¨é«åº¦
    height: String,

    // æ©å± el-table éç½®
    options: {
      type: Object,
      default: () => ({})
    }
  },

  data () {
    return {
      mounted: false,
      loading: false, // æ¯å¦ loading
      queryData: null, // å è½½åè¡¨æ°æ®
      pagin: this.getPagin(), // åé¡µéç½®
      sorts: this.defaultSort || {}, // æåºéç½®
      filters: {} // ç­ééç½®
    }
  },

  computed: {
    // æ¯å¦éææ°æ®
    isStatic () {
      return typeof this.query !== 'function'
    },

    // åè¡¨å±ç¤ºæ°æ®
    // TODO: éææ°æ®åé¡µè¿æ»¤æåº
    tableData () {
      return (this.isStatic ? this.data : this.queryData) || []
    },

    // éæ©
    selection () {
      return this.mounted ? this.$refs.table.selection : []
    }
  },

  created () {
    this.initColumns()
  },

  methods: {
    // ElTable æè½½
    tableMounted () {
      this.mounted = true

      // ç§»å¨åé¡µèç¹
      this.$el.appendChild(this.$refs.pagin.$el)

      this.filters = this.getFilters()

      // èªå¨å è½½åè¡¨
      this.autoLoad && this.load()
    },

    // åå§åå­æ®µ
    initColumns () {
      this.columns.forEach(item => {
        if (item.renderOption) {
          item.render = this.getOptionRender(item.renderOption)
        } else if (typeof item.formatter === 'string') {
          const formatter = this.getFilterFormatter(item.formatter)
          if (formatter) {
            item.formatter = formatter
          } else {
            delete item.formatter
          }
        }
      })
    },

    // éé¡¹æ¸²æå½æ°
    getOptionRender (option) {
      let options = typeof option === 'string'
        ? (this.$option && this.$option[option])
        : option

      if (!options) {
        console.warn('[FaTable]: æ ¹æ®éç½®ç renderOption æ æ³è·åå°éé¡¹', option)
        return ''
      }

      return (h, { row, value, index }) => {
        const match = options.find(item => item.value === value)

        if (match) {
          return <span class={match.class}>
            {match.text}
          </span>
        }

        return ''
      }
    },

    // è¿æ»¤å¨æ ¼å¼å
    getFilterFormatter (filterName) {
      // è·åå¨å±è¿æ»¤å¨
      const filter = Vue.filter(filterName)

      if (filter) {
        return (row, column, value, index) => filter(value)
      }
      return undefined
    },

    // å­æ®µ/è¡¨å¤´ä½ç¨åç»å®
    bindScope ({ row, column, $index: index }) {
      if (row) {
        const value = row[column.property]
        return { row, column, value, index }
      }
      return { column, index }
    },

    // å è½½æ°æ®
    load () {
      if (this.isStatic) return

      const { pagin, filters, sorts } = this
      this.loading = true

      this.query({ pagin, filters, sorts }).then(({ data, total }) => {
        this.queryData = data
        pagin.total = total
      }).finally(() => {
        this.loading = false
      })
    },

    // è·ååé¡µéç½®
    getPagin () {
      return { ...defaultPager, ...this.pager }
    },

    // æ´æ¹åé¡µæ°
    changeSize (size) {
      Object.assign(this.pagin, {
        pageSize: size,
        currentPage: 1
      })

      this.load()
    },

    // å½åé¡µè·³è½¬
    jumpPage (page = 1) {
      this.pagin.currentPage = page
      this.load()
    },

    // æåºæ´æ¹
    sortChange (sort) {
      this.$emit('sort-change', sort)
      if (this.isStatic) return
      this.sorts = sort
      this.jumpPage()
    },

    // è·åç­éæ°æ®
    getFilters () {
      const filters = {}
      this.$refs.table.columns.forEach(({ property: prop, filteredValue: value }) => {
        if (value && value.length) {
          filters[prop] = value
        }
      })
      return filters
    },

    // ç­éæ´æ¹
    filterChange (filters) {
      this.$emit('filter-change', filters)
      if (this.isStatic) return
      this.filters = this.getFilters()
      this.jumpPage()
    }
  }
}
